import React,{Component} from 'react';

import carrinho from '../../images/carrinho.svg';

import {Container,Nav,LinkNav} from './styled';
import Logo from '../Logo';
import $ from 'jquery';

class Header extends Component{
  render(){
    return (
      <Container className="Header">
          <Logo/>
          <Nav className="nav">
              <LinkNav href="#/produto/faciais">tratamentos faciais</LinkNav>
              <LinkNav href="#/produto/corporais">tratamentos corporais</LinkNav>
              <LinkNav href="#/produto/massagens">massagens</LinkNav>
          </Nav>
          <a href="#/carrinho" className="User"><img src={carrinho} alt=""/></a>
      </Container>
    );
  }
}

export default Header;
