import styled from 'styled-components';
import arrow from './../../images/arrow.svg';
import {Link} from 'react-router-dom';


export const Container = styled.header`
    background-color: var(--primaryColor);
    position:fixed;
    min-width: 100%;
    min-height: 80px;
    overflow:hidden;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 10px;
    z-index:1000;

    .LogoSite{
        color:white;
        display: flex;
        flex-direction: row;
        align-items: center;
        text-decoration:none;
        
        
        img{
            height: 70px;
        }

        .texto{
            display: flex;
            flex-direction: column;
            text-align: right;
            font-size: .8em;
            text-transform: uppercase;

            .principal{
                font-weight: bold;
                font-size: 2em;
            }
        
        }
    }

    .User img{
        height: 60px;
        padding:2px;
        border-radius:100%;
    }
`;


export const Nav = styled.div`
    display:flex;
    justify-content:space-between;
    align-items:center;
    

    

`;

export const LinkNav = styled.a`

    padding: 0px 5vw;
    color:var(--textPrimaryColor);
    font-weight: 500;
    display:flex;
    justify-content:center;
    align-items:center; 
    text-align:center;
    text-transform:uppercase;
    cursor: pointer;
    text-decoration:none;


    
    :after{
        clear: both;
        content: ' ';
        display: inline-block;
        padding: 10px;
        background-image: url(${arrow});
        background-size:100%;
        background-repeat:no-repeat;
        background-position:center;
        margin-left:10px;
    }

    @media screen and (max-width: 1024px) {
        /*estilos*/
        display:none;
    }

`;
