import React  from 'react';
import {HashRouter , Switch, Route } from 'react-router-dom';

import LazyImport from './Pages/Loader';


import Erro from './Pages/Error';
import Header from './Components/Header';
import Footer from './Components/Footer';

const ReactLazyPreload = importStatement => { 
  const Component = React.lazy (importStatement); 
  Component.preload = importStatement; 
  return Component; 
};

const Home = ReactLazyPreload(() => import('./Pages/Home'));
  
const Product = ReactLazyPreload(() => import('./Pages/Product'));
  
const Car = ReactLazyPreload(() => import('./Pages/Car'));




export default function Routes(){

        return (
        <HashRouter>
          <React.Suspense fallback={<LazyImport/>}>
            <Header/>
            <Switch>
                <Route exact path={"/"} component={Home}/>
                <Route path={"/produto/:id"} component={Product}/>
                <Route exact path={"/carrinho"} component={Car}/>
                <Route path={"*"} component={Erro}/>
            </Switch>
            <Footer/>
          </React.Suspense>
        </HashRouter>
        );

}
