import styled from 'styled-components';

export const Container = styled.footer`
    background-color: var(--primaryColor);
    min-width: 100vw;
    min-height: auto;
    overflow:hidden;
    display:flex;
    flex-direction:column;

`;

export const Map = styled.iframe`
border:none;

`;

export const Conteudo = styled.div`
min-height:80px;
display:grid;
grid-template-columns: 1fr auto 1fr;
padding:10px;
color:var(--backColor);
    
    .logo{
        display:flex;
        justify-content:center;
        align-items:center;

    }
    .info{
        display:grid;
        grid-template-rows:auto 1fr;
        grid-template-columns:repeat(3,auto);
        grid-column-gap:10px;
        grid-row-gap:10px;
        justify-items:center;
        align-items:center;
        font-weight:bolder;

        .title{
            font-size:1.2em;
            font-weight:bolder;
            grid-column:span 3;
            justify-self:start;
        }
        .traco{
            background-color:var(--backColor);
            height:100%;
            width: 5px;
            border-radius: 5px;
        }

    }
    
    .social{
        display:flex;
        justify-content:center;
        
        *{
            cursor: pointer;
        }

        .insta{
            display:flex;
            flex-direction:column;
            font-weight:bolder;
            color:white;
            text-decoration:none;
            align-items:center;
            margin:0px 15px;
            
            img{
                height:40px;
                margin:5px;
            }

        }
    }
    @media screen and (max-width: 1024px) {
        /*estilos*/
        display:flex;
        flex-direction:column;
        
        .info{
            display:flex;
            flex-direction:column;
            *{
                margin: 5px auto;
            }
            .traco{
                width: 80%;
                height:5px;
            }
        }

    }
`;