
import styled from 'styled-components';
import {Link} from 'react-router-dom';


export const LogoSite= styled(Link)`
    color:white;
    display: flex;
    flex-direction: row;
    align-items: center;
    text-decoration:none;
    
    
    img{
        height: 70px;
    }

    .texto{
        display: flex;
        flex-direction: column;
        text-align: right;
        font-size: .8em;
        text-transform: uppercase;

        .principal{
            font-weight: bold;
            font-size: 2em;
        }
    
    }

`;